@import url(https://fonts.googleapis.com/css?family=Nothing+You+Could+Do|Special+Elite);
* { box-sizing: border-box; }

img {
  max-width: 40%;
  max-height: 40%;
}

.video-background {
  background: #000;
  position: fixed;
  top: 0; right: 0; bottom: 0; left: 0;
  z-index: -99;
}
.video-foreground,
.video-background iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}


#vidtop-content {
	top: 0;
	color: #fff;
}
.vid-info { position: absolute; top: 0; right: 0; width: 33%; background: rgba(0,0,0,0.3); color: #fff; padding: 1rem; font-family: Avenir, Helvetica, sans-serif; }
.vid-info h1 { font-size: 2rem; font-weight: 700; margin-top: 0; line-height: 1.2; }
.vid-info a { display: block; color: #fff; text-decoration: none; background: rgba(0,0,0,0.5); transition: .6s background; border-bottom: none; margin: 1rem auto; text-align: center; }
@media (min-aspect-ratio: 16/9) {
  .video-foreground { height: 300%; top: -100%; }
}
@media (max-aspect-ratio: 16/9) {
  .video-foreground { width: 300%; left: -100%; }
}
@media all and (max-width: 600px) {
.vid-info { width: 50%; padding: .5rem; }
.vid-info h1 { margin-bottom: .2rem; }
}
@media all and (max-width: 500px) {
.vid-info .acronym { display: none; }
}
.popup {  
position: fixed;  
width: 100%;  
height: 100%;  
top: 0;  
left: 0;  
right: 0;  
bottom: 0;  
margin-top: 200px;  
color: red;  
}  

.popup\_inner {  
position: absolute;  
left: 25%;  
right: 25%;  
top: 25%;  
bottom: 25%;  
margin: auto;  
border-radius: 20px;
background: white;  
}

.Form {  
    position: fixed;  
    width: 50%;  
    height: 50%;  
    left: 25%;  
    right: 25%;  
    top: 75%;  
    bottom: 15%; 
    margin-top: auto;  
    background-color: black;
    }  

.formComponents{
    box-sizing: border-box;
    margin-top: 20px;
}

#typezone {
    border: 1px white solid;
    font-family: Arial, Helvetica, sans-serif;
    line-height: 1.4;
    font-size: small;
    resize: none; 
    width: 80%
}


* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    touch-action: none;
  }


  
a{
color: #333;
text-decoration: none;
}

@-webkit-keyframes shakes {
    0% {
        transform: translate(2px, 1px) rotate(1deg);
    }
    50% {
        transform: translate(-1px, 1px) rotate(0deg);
    }
    100% {
        transform: translate(1px, -2px) rotate(0deg);
    }
}

@keyframes shakes {
    0% {
        transform: translate(2px, 1px) rotate(1deg);
    }
    50% {
        transform: translate(-1px, 1px) rotate(0deg);
    }
    100% {
        transform: translate(1px, -2px) rotate(0deg);
    }
}

@-webkit-keyframes bigshakes {
    0% {
        transform: translate(2px, -8px) rotate(-1deg);
    }
    50% {
        transform: translate(-9px, 10px) rotate(-18deg);
    }
    100% {
        transform: translate(6px, -2px) rotate(1deg);
    }
}

@keyframes bigshakes {
    0% {
        transform: translate(2px, -8px) rotate(-1deg);
    }
    50% {
        transform: translate(-9px, 10px) rotate(-18deg);
    }
    100% {
        transform: translate(6px, -2px) rotate(1deg);
    }
}

@-webkit-keyframes glow {
    0% {
        box-shadow: 0px 0px 40px rgba(255, 255, 255, 0.5);
    }
    50% {
        box-shadow: 0px 0px 20px rgba(255, 255, 255, 0.35);
    }
    100% {
        box-shadow: 0px 0px 40px rgba(255, 255, 255, 0.5);
    }
}

@keyframes glow {
    0% {
        box-shadow: 0px 0px 40px rgba(255, 255, 255, 0.5);
    }
    50% {
        box-shadow: 0px 0px 20px rgba(255, 255, 255, 0.35);
    }
    100% {
        box-shadow: 0px 0px 40px rgba(255, 255, 255, 0.5);
    }
}

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    font-size: 100%;
    vertical-align: baseline;
    background: transparent;
    font-family: 'Nothing You Could Do';
    color: #888;
}

html {
    background: #444;
}

body {
    background: #000;
    height: auto;
    background: rgb(94, 94, 94);
    background: radial-gradient(ellipse at center, rgba(94, 94, 94, 1) 0%, rgba(0, 0, 0, 1) 100%);
}

h1,
h2,
h3,
h4,
h5,
h6 {
    color: #ccc;
    font-size: 30px;
    font-family: 'Special Elite';
}

a{
    display: inline-block;
    color: #a00;
    font-weight: bold;
    text-decoration: none;
    transition: color 0.25s, background 0.25s, background-color 0.25s, text-shadow 0.25s;
    transform: translate(0px, 0px) rotate(0deg);
}



a:hover, button:hover{
    text-shadow: 0px 0px 18px rgba(255, 0, 0, 1);
    -webkit-animation: shakes 0.1s linear infinite;
            animation: shakes 0.1s linear infinite;
}





